<template>
    <div>
        <div class="row">
            <div class="col-lg-3 col-xs-6">
                <!-- small box -->
                <div class="small-box bg-green">
                    <div class="inner">
                        <h3>爬取程序</h3>
                        <p>Running</p>
                    </div>
                    <div class="icon">
                        <i class="fa fa-hourglass-2"></i>
                    </div>
                </div>
            </div>
            <!-- ./col -->
            <div class="col-lg-3 col-xs-6">
                <!-- small box -->
                <div class="small-box bg-aqua">
                    <div class="inner">
                        <h3>{{ storeInfo['active_count'] }}</h3>
                        <p>活跃量</p>
                    </div>
                    <div class="icon">
                        <i class="fa fa-certificate"></i>
                    </div>
                </div>
            </div>
            <!-- ./col -->
            <div class="col-lg-3 col-xs-6">
                <!-- small box -->
                <div class="small-box bg-yellow">
                    <div class="inner">
                        <h3>{{ storeInfo['total_count'] }}</h3>
                        <p>库存量</p>
                    </div>
                    <div class="icon">
                        <i class="fa fa-bank"></i>
                    </div>
                </div>
            </div>
            <!-- ./col -->
            <div class="col-lg-3 col-xs-6">
                <!-- small box -->
                <div class="small-box bg-red">
                    <div class="inner">
                        <h3>检测程序</h3>
                        <p>Running</p>
                    </div>
                    <div class="icon">
                        <i class="fa fa-plus-circle"></i>
                    </div>
                </div>
            </div>
            <!-- ./col -->
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            storeInfo: {}
        };
    },
    created() {
        this.getStoreInfo();
    },
    methods: {
        getStoreInfo() {
            this.$http.get("dashboard.store_info").then(res => {
                if (res.body.status) {
                    this.storeInfo = res.body.store_info;
                } else {
                    this.$notify({
                        title: res.body.message,
                        type: "warning"
                    });
                }
            });
        }
    }
};
</script>

<style lang="">
</style>




